.horizontalSeparator{
    width: 80%;
    height: 1px;
    background-color: #747474;
    display: flex;
    align-items: center;
    bottom:0;
}
.contentLive{
    width:100%;
    height:60px;
    color: #EEE;
}
.contentLiveLink{
    text-decoration:none;
    color: #EEE;
    font-size:1.2vw;
    font-weight:bold;
    padding-left:8px;
    display: flex;
    justify-content:flex-start;
    align-items:center;
    height:100%;
    width: 100%;
}
.sportDiv{
    display:flex;
    align-items: center;
    justify-content: center;
}
.sportIcon{
    width:24px;
    height:24px;
    padding-right:20px;
}